body {
  margin: 0;
  padding: 0;
  font-family: 'Roboto', sans-serif;
  height: 100%;
}

.native-content img:focus,
.native-content input:focus,
.native-content textarea:focus,
.native-content section:focus,
.native-content div:focus,
.native-content p:focus,
.native-content button:focus {
  outline: none;
}

.logo-wrapper {
  -webkit-animation: logo-wrapper-spin infinite 20s linear;
          animation: logo-wrapper-spin infinite 20s linear;
  text-align: left;
  align-self: center;
}

@-webkit-keyframes logo-wrapper-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes logo-wrapper-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.logo-hover:hover path,
.logo-hover:hover polygon {
  fill: #f40ef0;
  fill-opacity: 0.6;
}

.logo {
  fill: #f40ef0;
  fill-opacity: 0.5;
  stroke: #000000;
}

.logo-native svg {
  width: 35px;
  height: 35px;
}

.App-container {
  text-align: center;
}

.App-header,
.App-header-native {
  background-color: #111; /* #222; */
  display: flex;
  padding-top: 15px;
  padding-bottom: 15px;
  padding-left: 5%;
  padding-right: 5%;
  color: white;
}

.App-header-native {
  padding-top: 5px;
  padding-bottom: 5px;
}

.App-body-native {
  padding-bottom: 72px; /* size of the tab footer */
}

.App-footer {
  width: 100%;
  position: fixed;
  left: 0;
  right: 0;
  bottom: 0;
}

.App-title {
  font-size: 1.5em;
}

.App-intro {
  font-size: large;
}

.navigation {
  display: block;
}
.routeGroup {
  list-style: none;
  display: flex;
  flex: 2 1;
  justify-content: center;
  align-self: center;
  margin: 0;
}

.nativeRouteGroup {
  background-color: #111; /* #222; */
  list-style: none;
  display: flex;
  justify-content: center;
  align-self: center;
  margin: 0;
  padding: 0;
}

ul.routeGroup li a,
ul.nativeRouteGroup li a {
  color: white;
  text-decoration: none;
}

ul.routeGroup li a p {
  font-size: 1.2em;
  padding: 15px;
  margin: 0;
}

ul.nativeRouteGroup li a {
  display: block;
  padding: 10px 20px;
}

@media only screen and (max-width: 360px) {
  ul.nativeRouteGroup li a {
    padding: 0.4rem 0.9rem;
  }
}

ul.nativeRouteGroup li a i {
  font-size: 2em;
  display: block;
  padding: 0;
}

ul.nativeRouteGroup li a p {
  margin: 0;
  padding-top: 5px;
  font-size: 0.8em;
  display: block;
}

ul.routeGroup li a:hover,
ul.nativeRouteGroup li a:hover {
  color: #f4adea;
  opacity: 0.9;
}

.socialGroup {
  list-style: none;
  display: flex;
  flex: 1 1;
  justify-content: flex-end;
  margin: 0;
  align-items: center;
}

.hitSlop {
  padding: 15px;
}

.socialhover:hover path {
  fill: #f4aeea;
}

