    display: none;
  }
} */

/* Loader */

@-webkit-keyframes placeHolderShimmer {
  0% {
    background-position: -468px 0;
  }
  100% {
    background-position: 468px 0;
  }
}

@keyframes placeHolderShimmer {
  0% {
    background-position: -468px 0;
  }
  100% {
    background-position: 468px 0;
  }
}

.animated-loader {
  -webkit-animation-duration: 1s;
          animation-duration: 1s;
  -webkit-animation-fill-mode: forwards;
          animation-fill-mode: forwards;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-name: placeHolderShimmer;
          animation-name: placeHolderShimmer;
  -webkit-animation-timing-function: linear;
          animation-timing-function: linear;
  background: #f6f7f8;
  background: linear-gradient(to right, #eeeeee 8%, #dddddd 18%, #eeeeee 33%);
}

/* Basic Page */

.animated-title {
  margin: 4.5rem 0 2rem 5%;
  height: 4em;
  width: 20%;
}

.animated-intro {
  margin: 0 0 1rem 5%;
  height: 2em;
  width: 60%;
}

.intro2 {
  margin-top: 1rem;
  width: 55%;
  margin-bottom: 2rem;
}

.animated-copy {
  margin: 0 0 0.4rem 5%;
  height: 1em;
}

.copy1,
.copy7 {
  width: 70%;
}

.copy2,
.copy9 {
  width: 68%;
}

.copy3,
.copy8 {
  width: 64%;
}

.copy4,
.copy6 {
  width: 67%;
}

.copy5,
.copy10 {
  width: 62%;
}

.animated-title.native-content {
  margin: 2rem 0 1.5rem 5%;
  height: 2em;
  width: 45%;
}

.animated-intro.native-content {
  margin: 0 0 0.5rem 5%;
  height: 1em;
  width: 90%;
}

.animated-intro.intro2.native-content {
  margin: 0 0 1.5rem 5%;
  height: 1em;
  width: 85%;
}

.animated-copy.native-content {
  height: 0.5em;
}

.copy1.native-content,
.copy2.native-content,
.copy3.native-content,
.copy4.native-content,
.copy5.native-content,
.copy6.native-content,
.copy7.native-content,
.copy8.native-content,
.copy9.native-content,
.copy10.native-content {
  width: 90%;
}

/* Contact Form */

.animated-email {
  height: 3em;
  margin-bottom: 2rem;
  width: 100%;
}

.animated-message {
  height: 9em;
  width: 100%;
}

